import { useEffect } from '@wordpress/element';
import { registerPlugin } from '@wordpress/plugins';
import { PluginPostStatusInfo } from '@wordpress/edit-post';
import { ToggleControl } from '@wordpress/components';

import { __ } from '@wp-plugins/i18n';

import EditModal from './EditModal';
import { WithDataProps } from './types';
import { withData } from './withData';

const PostToTelegram: React.FC<WithDataProps> = ({ data, isDirty, isSaving, savedData, updateField }) => {
	useEffect(() => {
		if (isDirty && !isSaving && !savedData) {
			updateField('send2tg')(data.send2tg);
		}
	}, [data.send2tg, isDirty, isSaving, savedData, updateField]);

	return (
		<PluginPostStatusInfo>
			<ToggleControl
				// the basic switch
				label={__('Send to Telegram')}
				checked={data.send2tg}
				onChange={updateField('send2tg')}
			/>
			<EditModal />
			&nbsp;
		</PluginPostStatusInfo>
	);
};

registerPlugin('wptelegram-post-to-telegram', {
	icon: null,
	render: withData(PostToTelegram),
});
