import { compose } from '@wordpress/compose';
import { withDispatch, withSelect } from '@wordpress/data';

import { __KEY__ } from './constants';
import { DataShape, WithDataProps } from './types';

let DEFAULT_DATA: DataShape = {
	channels: [],
	delay: '0',
	disable_notification: false,
	message_template: '',
	override_switch: false,
	send2tg: true,
	...window.wptelegram?.savedSettings,
};

type WithData = <P extends WithDataProps>(component: React.ComponentType<P>) => React.ComponentType<Partial<P>>;

export const withData: WithData = compose([
	withSelect(
		(select): Partial<WithDataProps> => {
			const { isSavingPost, isPublishingPost, isEditedPostDirty, getEditedPostAttribute } = select('core/editor');

			const savedData = getEditedPostAttribute<DataShape>(__KEY__);

			return {
				data: savedData || DEFAULT_DATA,
				isDirty: isEditedPostDirty<boolean>(),
				isSaving: isSavingPost() || isPublishingPost(),
				savedData,
			};
		}
	),
	withDispatch(
		(dispatch, _, { select }): Partial<WithDataProps> => {
			const updateField: WithDataProps['updateField'] = (field) => {
				return (value) => {
					const { getEditedPostAttribute } = select('core/editor');
					const { editPost } = dispatch('core/editor');

					const data = getEditedPostAttribute<DataShape>(__KEY__) || DEFAULT_DATA;

					const newData = { ...data, [field]: value };

					editPost({ [__KEY__]: newData }, { undoIgnore: true });

					// Update default data to ensure re-renders don't reset the data
					DEFAULT_DATA = newData;
				};
			};
			return { updateField };
		}
	),
]);
